export var SCREEN_WIDTH = {
    IS_SMALL: () => { return window.innerWidth <= 600 },
    IS_MEDIUM: () => { return window.innerWidth > 600 && window.innerWidth <= 992 },
    IS_LARGE: () => { return window.innerWidth > 993 },
    IS_LAND: () => {
        return IS_MOBILE && window.innerWidth > window.innerHeight;
    }
}
